body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#drum-machine {
  background-color: bisque;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

#display {
  width: 500px;
  height: 360px;
  background-color: #FFC600;
  border-radius: 5px;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  padding: 30px;
}

.drum-pad {
  width: 80px;
  height: 80px;
  border-radius: 5px;
  border: none;
  background: black;
  margin: 0 33px;
  color: white;
  flex: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.instrument-description {
  position: absolute;
  left: 6%;
  transform: translateY(-50%);
  top: 50%;
  width: 300px;
  height: 300px;
  border-radius: 5px;
  color: white;
  background-color: cornflowerblue;
  display: flex;
  justify-content: center;
  align-items: center;
}
